// XR: Se genera automáticamente el campo slug a partir del nombre del proyecto . Ayuda a mantener todo automático y consistente, si luego se cambia el nombre del proyecto, se actualiza la ruta

const slugify = (str) =>
  str
    .toLowerCase()
    .normalize("NFD") // quita acentos
    .replace(/[\u0300-\u036f]/g, '')
    .replace(/\s+/g, '-')
    .replace(/[^\w\-]+/g, '')


