import React from 'react';
import { Container, Row, Col } from 'react-bootstrap';
import { useParams, Link } from 'react-router-dom';
import { projects } from '../portfolio';
import ProjectPDFViewer from '../components/PDFViewer/ProjectPDFViewer';
import { FiArrowLeft } from 'react-icons/fi';
import './ProjectPage.css'; // Asegúrate de importar el CSS

const ProjectPage = () => {
  const { slug } = useParams();
  const project = projects.find(p => p.slug === slug);

  if (!project) {
    return (
      <Container className="project-page">
        <Row className="mb-4">

        </Row>
        <Row>
          <Col>
            <h1>Proyecto no encontrado</h1>
          </Col>
        </Row>
      </Container>
    );
  }

  return (
    <Container fluid className="project-page">
      <Row className="mb-4">
        <Col>
          <Link to="/#projects" className="btn--back">
            <FiArrowLeft /> regresar
          </Link>
        </Col>
      </Row>

      <Row>
        <Col>
          <h1 className="project-page__title">{project.name}</h1>
          
          <div className="project-page__description">
            {project.detailedDescription || project.description}
          </div>
          
          {project.images && project.images.length > 0 && (
            <div className="project-page__images">
              <h3>Imágenes del proyecto</h3>
              <div className="project-page__images-container">
                {project.images.map((image, i) => (
                  <figure key={i} className="project-page__image-wrapper">
                    <img
                      src={image.url}
                      alt={image.caption || `${project.name} - imagen ${i + 1}`}
                      className="project-page__image"
                      loading="auto" // Para mejorar el rendimiento
                    />
                    {image.caption && (
                      <figcaption className="project-page__image-caption">
                        {image.caption}
                      </figcaption>
                    )}
                  </figure>
                ))}
              </div>
            </div>
          )}
          
          {project.stack && (
            <div className="project-page__stack">
              <h3>Herramientas empleadas</h3>
              <ul>
                {project.stack.map((tech, i) => (
                  <li key={i}>{tech}</li>
                ))}
              </ul>
            </div>
          )}

          {/* PDF del proyecto */}
          {project.pdfUrl && (
            <div className="project-page__pdf">
              <h3>Documentación</h3>
              <ProjectPDFViewer 
                pdfUrl={project.pdfUrl} 
                /*title={`Documento: ${project.name}`} /* Se puede colocar title={`Documento: ${project.name}`} pero genera conflicto en vista móvil, necesita arreglarse */ 
              />
            </div>
          )}
          
          {/* Enlaces del proyecto */}
          {project.sourceCode && (
            <div className="project-page__links">
              <a
                href={project.sourceCode}
                target="_blank"
                rel="noopener noreferrer"
                className="btn btn--outline"
              >
                Ver código
              </a>
              
              {project.liveDemo && (
                <a
                  href={project.liveDemo}
                  target="_blank"
                  rel="noopener noreferrer"
                  className="btn btn--outline"
                >
                  Demo en vivo
                </a>
              )}
            </div>
          )}
        </Col>
      </Row>
    </Container>
  );
};

export default ProjectPage;