import React, { useState } from 'react';
import { Document, Page, pdfjs } from 'react-pdf';
import { Container, Row, Col, Button } from 'react-bootstrap';
import { FiChevronLeft, FiChevronRight, FiZoomIn, FiZoomOut } from 'react-icons/fi';
import './CVPDFViewer.css';

// Configuración necesaria para react-pdf
pdfjs.GlobalWorkerOptions.workerSrc = `//cdnjs.cloudflare.com/ajax/libs/pdf.js/${pdfjs.version}/pdf.worker.min.js`;

const CVPDFViewer = ({ pdfUrl }) => {
  const [numPages, setNumPages] = useState(null);
  const [pageNumber, setPageNumber] = useState(1);
  const [scale, setScale] = useState(1.4);
  const [loading, setLoading] = useState(true);

  function onDocumentLoadSuccess({ numPages }) {
    setNumPages(numPages);
    setLoading(false);
  }

  function onDocumentLoadError(error) {
    console.error('Error al cargar el PDF:', error);
    setLoading(false);
  }

  const changePage = (offset) => {
    const newPageNumber = pageNumber + offset;
    if (newPageNumber >= 1 && newPageNumber <= numPages) {
      setPageNumber(newPageNumber);
    }
  }

  const adjustZoom = (delta) => {
    const newScale = Math.max(0.5, Math.min(2, scale + delta));
    setScale(newScale);
  }

  return (
    //XR: Agregué el div con margin y padding 0 para evitar el scroll , pero quuiza no sea necesario	
    <div style={{ margin: 0, padding: 0 }}> 
    <Container fluid className="pdf-container-p-0">

      <Row className="justify-content-center">
        <Col className="pdf-document-wrapper">
          {loading && <div className="pdf-loading">Cargando documento...</div>}
          
          <Document
            file={pdfUrl}
            onLoadSuccess={onDocumentLoadSuccess}
            onLoadError={onDocumentLoadError}
            loading={<div className="pdf-loading">Cargando documento...</div>}
            error={<div className="pdf-error">Error al cargar el PDF. Verifica la URL o el formato del archivo.</div>}
            className="pdf-document"
          >
            <Page 
              pageNumber={pageNumber} 
              scale={scale}
              className="pdf-page"
              renderTextLayer={false}
              renderAnnotationLayer={false}
            />
          </Document>
        </Col>
      </Row>

            <Row className="justify-content-center mb-3">
        <Col xs="auto">
          <div className="pdf-controls">
            <Button 
              className="btn--control"
              onClick={() => changePage(-1)} 
              disabled={pageNumber <= 1}
            >
              <FiChevronLeft /> 
            </Button>
            
            <span className="page-info">
              Página {pageNumber} de {numPages || '...'}
            </span>
            
            <Button 
              className="btn--control"
              onClick={() => changePage(1)} 
              disabled={!numPages || pageNumber >= numPages}
            >
               <FiChevronRight />
            </Button>
            
            <Button 
              className="btn--control"
              onClick={() => adjustZoom(-0.1)}
            >
              <FiZoomOut />
            </Button>
            
            <span className="zoom-info">
              {Math.round(scale * 100)}%
            </span>
            
            <Button 
              className="btn--control"
              onClick={() => adjustZoom(0.1)}
            >
              <FiZoomIn />
            </Button>
          </div>
        </Col>
      </Row>

    </Container>
    </div>
  );
};

export default CVPDFViewer;