import React, { useState } from 'react';
import { Document, Page, pdfjs } from 'react-pdf';
import { Button } from 'react-bootstrap';
import { FiChevronLeft, FiChevronRight } from 'react-icons/fi';
import './ProjectPDFViewer.css';

// Configuración necesaria para react-pdf
pdfjs.GlobalWorkerOptions.workerSrc = `//cdnjs.cloudflare.com/ajax/libs/pdf.js/${pdfjs.version}/pdf.worker.min.js`;

const ProjectPDFViewer = ({ pdfUrl, title }) => {
  const [numPages, setNumPages] = useState(null);
  const [pageNumber, setPageNumber] = useState(1);
  const [loading, setLoading] = useState(true);

  function onDocumentLoadSuccess({ numPages }) {
    setNumPages(numPages);
    setLoading(false);
  }

  const changePage = (offset) => {
    const newPageNumber = pageNumber + offset;
    if (newPageNumber >= 1 && newPageNumber <= numPages) {
      setPageNumber(newPageNumber);
    }
  }

  return (
    <div className="project-pdf-container">
      {title && <h3 className="project-pdf-title">{title}</h3>}
      
      <div className="project-pdf-document-wrapper">
        {loading && <div className="project-pdf-loading">Cargando documento...</div>}
        
        <Document
          file={pdfUrl}
          onLoadSuccess={onDocumentLoadSuccess}
          loading={<div className="project-pdf-loading">Cargando documento...</div>}
          error={<div className="project-pdf-error">Error al cargar el PDF.</div>}
          className="project-pdf-document"
        >
          <Page 
            pageNumber={pageNumber} 
            renderTextLayer={false}
            renderAnnotationLayer={false}
            className="project-pdf-page"
            width={window.innerWidth < 600 ? window.innerWidth - 40 : 600}
          />
        </Document>
      </div>
       <div className="project-pdf-controls">
        
        <Button 
          className="btn--control"
          onClick={() => changePage(-1)} 
          disabled={pageNumber <= 1}
        >
          <FiChevronLeft /> 
        </Button>
        
        <span className="project-page-info">
          Página {pageNumber} de {numPages || '...'}
        </span>
        
        <Button 
          className="btn--control"
          onClick={() => changePage(1)} 
          disabled={!numPages || pageNumber >= numPages}
        >
           <FiChevronRight />
        </Button>
      </div>
    </div>
  );
};

export default ProjectPDFViewer;